
body {
  margin: 0 0 0 0;
}
.encabezado {
  margin: 0 0 0 0;
  background: -webkit-gradient(
    linear,
    left top, right top,
    from(rgb(0, 36, 19)),
    to(rgba(9, 97, 121, 0.541))
  );
  background: linear-gradient(
    90deg,
    rgb(0, 36, 19) 0%,
    rgba(9, 97, 121, 0.541) 100%
  );
  height: 90px;
  padding: 0%;
}
#logo {
  width: 200px;
  position: relative;
  left: 42%;
  top: 10px;
}
nav {
  padding: 3px;
}
nav button {
  width: 130px;
  color: rgb(1, 42, 23);
  background: #a0c28fca;
  border: 1px solid #e5e7e9;
  border-radius: 5px;
  padding: 10px 20px;
}
nav button:hover {
  color: #472834;
  background: #e5e7e9;
  border: 2px solid #e5e7e9;
}
.menu {
  width: 100%;
}
.comp {
  width: 85%;
}
.main-component {
  height: 60vh;
  margin: 0%;
  padding: 0%;
  background: #fdfefe;
}
.footer {
  margin: 0 0 0 0;
  padding: 0;
  width: 100%;
  height: 10vh;
  min-height: 100px;
  background-color: rgba(9, 97, 121, 0.541);
}
.footer h2 {
  width: 100%;
  height: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  color: azure;
}
.menu button {
  height: 50px;
  width: 150px;
  background-color: rgb(178, 190, 66);
  color: white;
  font-size: 16px;
}
.botones {
  margin: 0 0 0 0;
}

#Perfil {
  width: 98%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  color: rgb(2, 71, 39);
}
.ListaDocumentos {
  margin-top: 20px;
}

#TablaDocumentos {
  border: 6px solid black;
  text-transform: capitalize;
  text-align: center;
}
#TablaVacia {
  width: 700px;
  text-align: left;
}
#TablaVacia th {
  padding: 30px;
  color: rgb(100, 167, 46);
  font-size: 20px;
}
#TablaDocumentos th {
  border: 1px solid black;
  background: -webkit-gradient(linear, left top, left bottom, from(rgb(3, 61, 34)), to(rgba(5, 72, 90, 0.541)));
  background: linear-gradient(rgb(3, 61, 34) 0%, rgba(5, 72, 90, 0.541) 100%);
  color: rgba(242, 242, 248, 0.931);
  width: 120px;
}
#TablaDocumentos td {
  border: 1px solid black;
}
.semaforo #verde {
  border-radius: 100%;
  width: 20px;
  height: 20px;
  background-color: green;
  position: relative;
  left: 50px;
}
.semaforo #amarillo {
  border-radius: 100%;
  width: 20px;
  height: 20px;
  background-color: yellow;
  position: relative;
  left: 50px;
}
.semaforo #rojo {
  border-radius: 100%;
  width: 20px;
  height: 20px;
  background-color: red;
  position: relative;
  left: 50px;
}





























































.AgregarDocumentos {
  color: rgb(2, 71, 39);
}
.AgregarDocumentos table {
  position: relative;
  left: 7%;
  border-spacing: 10px;
}
.AgregarDocumentos button {
  background-color: rgb(110, 108, 69);
  color: azure;
  -webkit-box-shadow: 5px;
          box-shadow: 5px;
  margin-top: 25px;
  width: 150px;
}
#Agregar {
  height: 60px;
  position: relative;
  left: 35%;
  border-radius: 5px;
}
